.profile {
  display: flex;
  align-items: center;
  max-width: 882px;
  padding: 39px 0 0;
  margin: 0 auto;
  column-gap: 30px;
  row-gap: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 920px) {
  .profile {
    flex-direction: column;
    padding: 42px 19px 0;
  }
}
